import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DevotionalbookComponent } from './devotionalbook.component';
import { BooklistComponent } from './booklist/booklist.component';
import { BookcontainerComponent } from './bookcontainer/bookcontainer.component';

const routes: Routes = [
  {
    path:"", 
    component:DevotionalbookComponent, 
    data:{},
    children: [
      {path:"",component:BooklistComponent},
      {path:"item/:book",component:BookcontainerComponent}
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DevotionalbookRoutingModule { }
