import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-booklistitem',
  templateUrl: './booklistitem.component.html',
  styleUrls: ['./booklistitem.component.css']
})
export class BooklistitemComponent implements OnInit {

  @Input() value:object;

  constructor(
    private router:Router
  ) { }

  ngOnInit() {
  }

  bookClickHandler(event:any){
    this.router.navigate([this.router.routerState.snapshot.url + '/item/' + event.route]);
  }

}
