import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { catchError, tap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class BookitemsService {

  private _bookItems:any;
  private _itemUrl:string = 'assets/json/books.json';
  private _bookData:any;
  private _bookPrefix = 'assets/books';

  constructor(
    private _http:HttpClient
  ) { }

  getBookItems():Observable<Object[]>{
    if(this._bookItems){
      return of(this._bookItems)
    } else {
      return this._http.get<Object[]>(this._itemUrl)
      .pipe(
        tap((data)=>{
          this._bookItems = data;
        }),
        catchError(this.handleError('error with getting module info', []))
      )
    }
  }

  getBookData(str:string):Observable<Object[]>{

    if(this._bookData){
      return of(this._bookData);
    } else {
      return this._http.get<Object[]>(this._bookPrefix + '/' + str.toLowerCase() + '.json')
      .pipe(
        tap((data)=>{
          this._bookData = data;
        }),
        catchError(this.handleError('error with getting module info', []))
      )
    }


  }

   /**
     * Handle Http operation that failed.
     * Let the app continue.
     * @param operation - name of the operation that failed
     * @param result - optional value to return as the observable result
     */
    private handleError<T>(operation = 'operation', result?: T) {
      return (error: any): Observable<T> => {

          // TODO: send the error to remote logging infrastructure
          console.error(error); // log to console instead

          // TODO: better job of transforming error for user consumption
          console.log(`${operation} failed: ${error.message}`);

          // Let the app keep running by returning an empty result.
          return of(result as T);
      };
  }


}
