import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { BookitemsService } from '../bookservice/bookitems.service';

@Component({
  selector: 'app-bookcontainer',
  templateUrl: './bookcontainer.component.html',
  styleUrls: ['./bookcontainer.component.css']
})
export class BookcontainerComponent implements OnInit {

  _bookData : Object
  _pageDisplay:boolean = true;
  _bookDispaly:boolean = false;
  _config: any;
  _collection = { count: 60, data: [] };

  constructor(
    private route : ActivatedRoute,
    private bookItemsService: BookitemsService
  ) { }

  ngOnInit() {
    const book = this.route.snapshot.paramMap.get('book');
    this.getBookData(book);
  }

  private getBookData(str:string){
    this.bookItemsService.getBookData(str)
    .subscribe(
      (data)=>{
        for(var i in data){
          if(data[i]['name']==str.toLocaleUpperCase()){
            this._bookData = data[i];
            if(this._pageDisplay){
              this.updatePaginationConfig();
            }
            break;
          }
        }
      }
    );

    
  }

  private updatePaginationConfig(){
    this._collection.count = this._bookData['page'].length;
    this._collection.data = this._bookData['page'];
    this._config = {
      itemsPerPage: 3,
      currentPage: 1,
      totalItems: this._collection.count
    }
  }

  pageChanged(event:any){
    this._config.currentPage = event;
  }

}
